.footer {
    border-top: 1px solid #e9e9e9;
    background-color: #f0f0f0;
    margin-top: 40px;
    color: #212121;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    height: auto;
    &__nav-list {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 50px;
    }
    &__nav-link {
        &:hover {
            a {
                color: #eb212e;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .footer {
        &__nav-list {
            flex-direction: column;
        }
    }
}