.shop-info {
    &__contacts {
        display: flex;
        flex-direction: column;
        text-align: initial;
        gap: 10px;
        margin-top: 20px;
        font-size: 18px;
        a:hover {
            color: #eb212e;
        }
    }
    &__logo {
        width: 270px;
    }

    &__head {
        display: flex;
        flex-direction: column;
        width: 270px;
        height: 256px;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.20), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
        padding: 20px;
        background-color: white;
        align-items: center;
        justify-content: center;
    }
    &__description {
        display: flex;
        text-align: initial;
        line-height: 1.4;
        font-size: 14px;
        border-top: 1px solid gray;
        margin-top: 18px;
        padding-top: 14px;
    }
}

@media screen and (max-width: 768px) {
    .shop-info {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &__head {
            width: 100%;
            box-sizing: border-box;
        }
    }
}