.promocode {
    background: #FFFFFF;
    border: 1px solid rgba(51,51,51,0.10);
    -webkit-box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.10);
    box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.10);
    border-radius: 3px;
    margin-bottom: 20px;
    padding: 20px;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: initial;
    &__container {
        display: flex;
    }
    &__link {
        font-size: 16px;
        display: block;
        color: #eb212e;
        letter-spacing: 0;
        line-height: 24px;
        margin-bottom: 0px;
        font-weight: bold;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    &__date {
        font-size: 12px;
        color: #A9A9A9;
        margin-bottom: 5px;
    }
    &__desc {
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        line-height: 20px;
        margin-bottom: 0;
        margin-bottom: 10px;
    }
    &__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        gap: 15px;
        margin-top: 15px;
        &-link {
            width: 50%;
            text-decoration: none;
        }
    }
    &__discont {
        display: block;
        width: 100px;
        height: 100px;
        position: relative;
        border: 1px solid rgba(51,51,51,0.10);
        border-radius: 3px;
        margin-right: 15px;
        font-size: 40px;
        color: #1E1E1E;
        min-width: 100px;
        min-height: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-decoration: none;
    }
    &__code {
        background: #F5F5F5;
        border: 1px solid rgba(51,51,51,0.10);
        height: 46px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50%;
        position: relative;
        box-sizing: border-box;
        &--hover-color {
            &:hover {
                color: #eb212e;
            }
        }
    }
}

.coupon-popup__close {
    padding: 0;
    background: transparent;
    position: absolute;
    width: 12px;
    height: 12px;
    top: 20px;
    right: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    &::before {
        position: absolute;
        top: -1px;
        content: "";
        height: 2px;
        width: 16.97px;
        background-color: #333;
        transform: rotate(46deg);
    }
    &::after {
        position: absolute;
        top: -1px;
        content: "";
        height: 2px;
        width: 16.97px;
        background-color: #333;
        transform: rotate(-46deg);
    }
}