.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content-block {
  margin-top: 110px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  gap: 30px;
  &__center {
    width: 780px;
  }
  &__right {
    width: 310px;
    float: right;
  }
  &__title {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 768px) {
  .content-block {
    width: 750px;
  }
}

@media screen and (max-width: 768px)  {
  .content-block {
    width: 93%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-right: 0;
    padding-left: 0;
    &__center {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
  }
}
@media only screen and (max-width: 830px) {
  .content-block {
    min-width: 320px;
  }
}

@media screen and (min-width: 992px) {
  .content-block {
    width: 970px;
  }
}

@media screen and (min-width: 1200px) {
  .content-block {
    width: 1170px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
